"use client";

import Link from "next/link";
import { ArrowLeft, Share2, Store, Home } from "lucide-react";
import { useRouter } from "next/navigation";

interface ProductHeaderProps {
  storeSlug?: string; // Optional: If we know the store, we link to it
}

export default function ProductHeader({ storeSlug }: ProductHeaderProps) {
  const router = useRouter();

  const handleShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: document.title,
          url: window.location.href,
        });
      } catch (err) { console.log(err); }
    } else {
      navigator.clipboard.writeText(window.location.href);
      alert("Link copied!");
    }
  };

  return (
    <div className="fixed top-0 left-0 right-0 z-50 p-4 flex justify-between items-center pointer-events-none">
      
      {/* 1. BACK BUTTON */}
      <button 
        onClick={() => router.back()} 
        className="pointer-events-auto bg-white/80 backdrop-blur-md p-3 rounded-full shadow-sm text-gray-900 hover:bg-white transition active:scale-95"
      >
        <ArrowLeft size={20} />
      </button>

      {/* 2. ACTIONS (Store & Share) */}
      <div className="flex gap-3">
        {/* If we have a store slug, show a Store Button */}
        {storeSlug && (
           <Link href={`/${storeSlug}`} className="pointer-events-auto bg-white/80 backdrop-blur-md p-3 rounded-full shadow-sm text-gray-900 hover:bg-white transition active:scale-95">
             <Store size={20} />
           </Link>
        )}

        <Link href="/" className="pointer-events-auto bg-white/80 backdrop-blur-md p-3 rounded-full shadow-sm text-gray-900 hover:bg-white transition active:scale-95">
           <Home size={20} />
        </Link>
        
        <button 
          onClick={handleShare}
          className="pointer-events-auto bg-white/80 backdrop-blur-md p-3 rounded-full shadow-sm text-gray-900 hover:bg-white transition active:scale-95"
        >
           <Share2 size={20} />
        </button>
      </div>
    </div>
  );
}